import { gql } from "@apollo/client";

/******** QUERY ********/
export const QueryAllDaos = gql`
  query MyQuery(
    $page: Int!
    $size: Int!
    $chainId: Int!
    $sort: String
    $active: Boolean!
  ) {
    allDaoList(
      page: { page: $page, size: $size, order: "desc", sort_field: $sort }
      req: { chain_id: $chainId, only_active: $active }
    ) {
      page
      size
      total
      data {
        dao_id
        desc
        handle
        et_holders
        is_like
        tav
        period
        name
        logo
        dao_version
        dao_type
        et_token_address
        et_token_type
        dao_address
        gp_member_count
      }
    }
  }
`;

