export const AddMyToken = gql`
  mutation MyMutation(
    $chainId: Int!
    $daoId: Int!
    $address: String!
    $symbol: String!
    $name: String!
  ) {
    addMyToken(
      req: {
        chain_id: $chainId
        dao_id: $daoId
        address: $address
        symbol: $symbol
        name: $name
        dao_version: 3
      }
    )
  }
`;
