const ButtonStyled = styled.span`
  display: inline-block;
  text-align: center;
  width: 111px;
  height: 36px;
  color: #ffffff;
  line-height: 36px;
  font-weight: 400;
  border-radius: 10px;
  font-size: 14px;
  cursor: pointer;
  color: #041735;
  cursor: ${({ pending }) => (pending ? "auto" : "pointer")};
  opacity: ${({ pending }) => (pending ? "0.5" : "1")};
`;

const AgreeButton = styled(ButtonStyled)`
  background: #79dfc4;
`;

const DisagreeButton = styled(ButtonStyled)`
  background: #ffa4d1;
  margin-left: 20px;
`;

const StatusButton = styled(ButtonStyled)`
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.32) 0%,
    rgba(255, 255, 255, 0.5) 100%
  );
`;

const Pagination = styled.div`
  text-align: right;
  margin: 0 40px;
`;

const Empty = styled(EmptyBox)`
  position: relative;
  min-height: unset;
  padding-top: 30px;
`;
