import useSigner from "hooks/useSigner";
import Done from "./Done";
import FailedModal from "./Failed";
import api from "api";
import { useEffect } from "react";
import { useMemo } from "react";
import { useDefiContext } from "components/provider/defiProvider";
import { getBalanceInUSD, calculateHealth, formatHealth } from "utils/aave";
import BigNumber from "bignumber.js";
import { valueToBigNumber } from "@aave/math-utils";
import { formatInput2bignumber } from "utils/utils";
import { useWeb3React } from "@web3-react/core";
import ApplyModal from "components/apps/swap/ApplyModal";
import showNotification, {
  NotificationType,
} from "components/common/notification";
import { TransType } from "components/transaction/checkTransaction";
import Button from "components/common/button";
import Max from "components/common/max";
import { formatError } from "api/aave";

const STATUS = {
  Default: 0,
  Success: 1,
  Failed: 2,
};

export default function WithdrawModal(props) {
  const { show, closeModal, userReserve } = props;
  const { t } = useTranslation();

  const { account } = useWeb3React();
  const {
    state: {
      userUsdData,
      usdPriceEth,
      userData: { currentLiquidationThreshold, healthFactor },
      userDebtReserves,
    },
  } = useDefiContext();
  const {
    dispatch,
    state: { exploreScan },
  } = useAppContext();
  const {
    state: { daoId, componentAddressMap },
  } = useDaoContext();
  const daoOrg = useDaoOrgContract(componentAddressMap.get(DAO_ORG));
  const signer = useSigner();
  const grantContract = useGrantManagerContract(
    componentAddressMap.get(GRANT_MANAGER)
  );

  const [balance, setBalance] = useState(0);
  const [amount, setAmount] = useState();

  const [status, setStatus] = useState(STATUS.Default);
  const [txHash, setTxHash] = useState();
  const [errorMsg, setErrorMsg] = useState("");
  const [showApply, setShowApply] = useState(false);

  const [newHealth, setNewHealth] = useState(0);

  const reserve = useMemo(() => {
    return userReserve ? userReserve.reserve : {};
  }, [userReserve]);

  const heathDisplay = useMemo(() => {
    if (healthFactor) {
      return formatHealth(
        BigNumber(healthFactor.toString()).div(BigNumber(1e18))
      );
    } else {
      return 0;
    }
  }, [healthFactor]);
  
  const hasDebt = useMemo(() => {
    return !!userDebtReserves.length;
  }, [userDebtReserves]);

  const txLink = useMemo(() => {
    if (exploreScan && txHash) {
      return `${exploreScan}tx/${txHash}`;
    }
    return "";
  }, [txHash, exploreScan]);

  const onClickWithdraw = async () => {
    console.log("max:", userReserve.maxWithdrawAmount);
    if (amount > userReserve.maxWithdrawAmount) {
      dispatch({ type: 'MSGTYPE', payload: { msg: `${t("defi.withdraw.avaliableMSG")}: ${userReserve.maxWithdrawAmount} ${reserve.symbol}` } });
      return
    }
    let unlimitPermission = false;
    const vaultAddress = componentAddressMap.get(VAULT_MANAGER);
    try {
      unlimitPermission = await grantContract.checkMethodPermission(
        account,
        vaultAddress,
        "GP_Loan_unlimited"
      );
    } catch (error) {
      console.error("checkMethodPermission GP_Buy_unlimited failed", error);
    }
    if (!unlimitPermission) {
      try {
        const data = await grantContract.checkMethodPermission(
          account,
          vaultAddress,
          "GP_Loan"
        );
        if (!data) {
          setShowApply(true);
          return;
        }
      } catch (error) {
        console.error("checkMethodPermission failed", error);
      }

      try {
        const atokenAddress = reserve.aToken && reserve.aToken.id;
        const data = await grantContract.getTokenLimit(
          1,
          atokenAddress,
          account,
          ethers.constants.AddressZero
        );
        const delta = data[1].sub(data[0]);
        if (
          BigNumber(amount).gt(
            BigNumber(
              ethers.utils.formatUnits(delta, reserve.decimals).toString()
            )
          )
        ) {
          dispatch({
            type: "MSGTYPE",
            payload: { msg: t("defi.withdraw.Insufficient") },
          });
          return;
        }
      } catch (error) {
        console.error("getTokenLimit failed", error);
      }
    }

    const params = { amount: amount, symbol: reserve.symbol };

    try {
      const amountBN = formatInput2bignumber(amount, reserve.decimals);
      const res = await api.aave.aaveWithdraw(daoOrg, signer, {
        amount: amountBN,
        tokenAddress: reserve.underlyingAsset,
      });
      const txHash = res.hash;

      const nid = showNotification(
        NotificationType.Loading,
        t("Notification.DefiWithdraw", params),
        `${exploreScan}tx/${txHash}`
      );

      dispatch({
        type: "PUT_TRANSACTION",
        payload: {
          notifyId: nid,
          hash: txHash,
          trans: res,
          status: 0,
          type: TransType.DefiWithdraw,
          data: {
            daoId,
            ...params,
          },
        },
      });
      closeModal();
      
    } catch (error) {
      console.error("aaveWithdraw failed", error);
      const msg = error?.reason || error?.data?.message;
      if (msg) {
        dispatch({
          type: "MSGTYPE",
          payload: { msg: formatError(msg) },
        });
      }
      // setErrorMsg(error?.error?.message);
      // showNotification(
      //   NotificationType.Fail,
      //   t("Notification.DefiWithdrawFailed")
      // );
    } 
  };

  const closeDone = () => {
    setStatus(STATUS.Default);
    closeModal();
  };

  const closeFailed = () => {
    setStatus(STATUS.Default);
    setTxHash();
    closeModal();
  };

  const remainSupply = useMemo(() => {
    let delta = BigNumber(balance).minus(BigNumber(amount || 0));
    delta = delta.toNumber();
    return delta > 0 ? getShortDisplay(delta) : 0;
  }, [balance, amount]);

  useEffect(() => {
    if (!reserve) {
      return;
    }
    if (!amount || amount < 0) {
      setNewHealth(0);
      return;
    }
    const { totalCollateralUsdVal, totalDebtUsdVal } = userUsdData;
    if (!totalCollateralUsdVal || !totalDebtUsdVal) {
      return;
    }
    const totalCollateralUSD_BN = valueToBigNumber(totalCollateralUsdVal);
    const totalBorrowsUSD_BN = valueToBigNumber(totalDebtUsdVal);

    const currentLiquidationThreshold_BN = valueToBigNumber(
      currentLiquidationThreshold.toString()
    );

    const amountBN = BigNumber(amount).shiftedBy(reserve.decimals);
    const amountUSD_BN = getBalanceInUSD(
      usdPriceEth,
      reserve.price.priceInEth,
      amountBN
    );

    const v = calculateHealth(
      totalCollateralUSD_BN.minus(amountUSD_BN.shiftedBy(-reserve.decimals)),
      totalBorrowsUSD_BN,
      currentLiquidationThreshold_BN
    );
    setNewHealth(formatHealth(BigNumber(v)));
  }, [
    amount,
    userUsdData,
    userReserve,
    currentLiquidationThreshold,
    usdPriceEth,
  ]);
  useEffect(() => {
    if (userReserve) {
      setBalance(Number(userReserve.balance));
    }
  }, [userReserve]);
  return (
    <>
      <General
        width="440"
        height="490"
        closeModal={closeModal}
        show={show}
        title={`${t("defi.withdraw.Withdraw")} ${reserve && reserve.symbol}`}
        titleBold
      >
        <Label className="first">{t("Amount")}</Label>
        <InputBoxBg>
          <div className="input">
            <InputStyled
              autoFocus
              value={amount}
              min={0}
              controls={false}
              onChange={(v) => setAmount(v)}
            />
            {/* <BalanceBg>1054.97</BalanceBg> */}
            {/* <PriceBox>
            $1.052.70 <span>(0.155%)</span>
          </PriceBox> */}
          </div>
          <div>
            <SelectBox>
              <span className="name">
                <img src={userReserve.logo} alt="" />
                {reserve && reserve.symbol}
              </span>
            </SelectBox>
            <RhtBalance>
              <div className="balance">
                {t("defi.withdraw.Balance")} :{" "}
                {formatBalanceDisplay(Number(userReserve.balance))}
              </div>
              <Max t={t} onMax={() => setAmount(balance)} />
            </RhtBalance>
          </div>
        </InputBoxBg>
        <TransactionBox>
          <Label>{t("defi.withdraw.overview")}</Label>
          <BorderBox>
            <dl>
              <dt> {t("defi.withdraw.Remaining")}</dt>
              <dd className="first">
                {remainSupply} {reserve && reserve.symbol}
              </dd>
            </dl>

            {hasDebt && (
              <dl>
                <dt>{t("defi.withdraw.HealthFactor")}</dt>
                <dd>
                  <div className="green">
                    {heathDisplay}{" "}
                    {amount > 0 && amount <= balance && (
                      <>
                        <img src={NextImg} className="arrow" alt="" />
                      </>
                    )}
                  </div>
                  <div className="tips">
                    {t("defi.withdraw.Liquidation")} &lt; 1.0
                  </div>
                </dd>
              </dl>
            )}
          </BorderBox>
        </TransactionBox>
        {/* <LastLine>
        <img src={GasImg} alt="" /> $1.77
      </LastLine> */}
        <BtnGroup>
          <Button
            disabled={!amount || amount < 0 || amount > balance}
            onClick={onClickWithdraw}
            primary
          >
            {t("defi.withdraw.Withdraw")} {reserve && reserve.symbol}
          </Button>
        </BtnGroup>
      </General>
      {status === STATUS.Success && (
        <Done
          amount={amount}
          title={t("defi.withdraw.Withdrawed")}
          symbol={reserve && reserve.symbol}
          handleClose={closeDone}
          txLink={txLink}
        />
      )}
      {status === STATUS.Failed && (
        <FailedModal
          title={t("defi.withdraw.Failed")}
          content={t("defi.withdraw.FailedContent")}
          subContent={errorMsg}
          handleClose={closeFailed}
        />
      )}
      {showApply && (
        <ApplyModal show={showApply} closeModal={() => setShowApply(false)} />
      )}
    </>
  );
}

